@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:wght@700&family=Roboto:ital,wght@0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700;900&display=swap);

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(255, 255, 255);
}

header {
  position: relative;
  text-align: center;
  background-image: url("https://www.rekupe.com/wp-content/uploads/2019/01/HP_Background.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 75vh;
}
header h1 {
  color: white;
  font-size: 58px;
  font-family: "PT Serif", serif;
  padding: 170px 0px 0px 0px;
  line-height: 1;
  letter-spacing: -2px;
}

header p {
  margin: 34px 0px 0px 0px;
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: white;
}
header button {
  color: white;
  background-color: #ed4385;
  font-family: "Roboto", sans-serif;
  border: none;
  border-radius: 40px;
  font-size: 18px;
  margin: 35px 0px 0px 0px;
  padding: 23px 40px 23px 40px;
  box-shadow: 0px 13px 22px -8px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px;
  transition: 0.2s;
}
header button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.2s;
  cursor: pointer;
}
header .Triangle {
  position: absolute;
  bottom: 0;
  border-style: solid;
  border-width: 100px 0 0 99vw;
  border-color: transparent transparent transparent #ffffff;
}
/* Animations */
header h1,
header p,
header button {
  position: relative;
  visibility: hidden;
  -webkit-animation-name: moveUp;
          animation-name: moveUp;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes moveUp {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    margin-bottom: 40px;
    top: 200px;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    margin-bottom: 0px;
    top: 0px;
    visibility: visible;
  }
}
@keyframes moveUp {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    margin-bottom: 40px;
    top: 200px;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    margin-bottom: 0px;
    top: 0px;
    visibility: visible;
  }
}

.Main {
  background-color: white;
  height: 80vh;
  z-index: 2;
  position: relative;
}
.Main .text {
  margin-top: 260px;
  text-align: center;
  font-family: "PT Serif", serif;
  font-size: 30px;
  color: #5722cd;
}
.Main p {
  margin-bottom: 40px;
}
.Main .underText {
  color: black;
  font-family: "Roboto", sans-serif;
  font-size: 21px;
  margin-top: 100px;
}
.Main .underText a {
  color: #5722cd;
  text-decoration: none;
}
.backgroundText {
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  color: #5822cd13;
  font-size: 600px;
  position: absolute;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  z-index: -1;
  overflow: hidden;
}

/* Animations */
.Main .text {
  visibility: hidden;
  -webkit-animation-name: easeIn;
          animation-name: easeIn;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes easeIn {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    visibility: visible;
  }
}
@keyframes easeIn {
  0% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 0;
    visibility: hidden;
  }
  100% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    opacity: 1;
    visibility: visible;
  }
}

nav {
  z-index: 2;
  position: relative;
  font-family: "Roboto", sans-serif;
}
nav ul {
  list-style-type: none;
  float: right;
  margin: 25px 265px 0px 20px;
}
nav ul li {
  display: inline;
  padding: 0px 20px 0px 20px;
  border-bottom: 4px solid transparent;
  transition: 0.1s;
  padding-bottom: 15px;
}
nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 15px;
}
nav ul li:hover {
  border-bottom: 5px solid #ed4385;
}

